export default {
	data: function() {
		return {
			mobile: '',
			msgcode: '',
			password1: '',
			password2: ''

		}
	},

	methods: {
		//获取短信验证码
		getVerifyCode: function() {
			this.$http({
				method: 'post',
				url: 'http://crane.u-xuan.com/Api/Login/sendMsgCode',
				data: {
					'mobile': that.mobile,

				},
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
				}
			}).then(function(response) {
				alert(response.data.msg)

			}).catch(function(err) {
				alert(err.data.msg)
			})

		},
		submit: function() {
			var that = this
			if(that.mobile == '') {
				layer.msg('手机号不能为空', { time: 2000 });
				return false;
			}

			var pattern = /^1[34578]\d{9}$/;

			if(pattern.test(that.mobile) == false) {
				layer.msg('手机号码格式不正确', { time: 1500 });
				return false;
			}

			if(that.msgcode == '') {
				layer.msg('验证码不能为空', { time: 2000 });
				return false;
			}

			layer.load(1, {
				shade: [0.1, '#000'] //0.1透明度的白色背景
			});

			//				$.ajax({
			//					url: "http://crane.u-xuan.com/Api/Login/sendMsgCode",
			//					dataType: 'json',
			//					type: 'post',
			//					data: {
			//						do: 'check',
			//						'mobile': that.mobile,
			//						'msgcode': that.msgcode,
			//
			//					},
			//					success: function(res) {
			//						layer.closeAll();
			//						if(res.status != 1) {
			//							layer.msg(res.msg, { time: 2000 });
			//
			//						} else {
			//							layer.msg(res.msg, { time: 2000 });
			//							that.$router.push('/main')
			//
			//						}
			//					}
			//				});
			that.$http({
				method: 'post',
				url: 'http://crane.u-xuan.com/Api/Login/person_register',
				data: {
					do: 'check',
					'mobile': that.mobile,
					'msgcode': that.msgcode,

				},
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
				}
			}).then(function(response) {
				alert(response.data.msg)
				that.$router.push('/main')

			}).catch(function(err) {
				alert(err.data.msg)
			})
			return false;

		}
	}
}



