
import XHeader from 'vux/src/components/x-header/index.vue'
;

export default {
	data: function() {
		return {
			code: '',
			name: '',
			address: ''
		}
	},
	components: {
		XHeader

	},
	methods: {
		sub: function() {
			var that = this

			$.ajax({
				url: 'urls/Engineering/engin_add',
				dataType: 'json',
				type: 'post',
				data: {
					code: that.code,
					name: that.name,
					address: that.address
				},
				success: function(e) {

					if(e.status == 1) {

					} else {
						$('.item').css('display', 'block')

					}
					return false;
				}
			})
		}

	}
}



