import Qs from 'qs'
//	import fetch from '../../assets/js/fetch'
export default {

	data: function() {
		return {
			data: {
				status: '',
				img: ''

			},
			src: '',
			username: null,
			password: null,
			code: null

		}
	},
	methods: {
		//获取验证码
		getvercode: function() {
			var that = this
			//				fetch('http://flyda/Api/Login/verify_code', {
			//						mode: 'cors',
			//
			//					}).then((res) => res.text())
			//					.then(data => {
			//						var datas = JSON.parse(data)
			//						that.src = datas.img
			//					})

			$.ajax({
				url: 'urls/Login/verify_code',
				dataType: 'json',
				type: 'post',
				data: {

				},
				success: function(e) {

					if(e.status == 1) {
						that.src = e.img

					} else {

					}
					return false;
				}
			})

		},
		//获取微信
		slideup: function() {
			$('#person_user').slideUp(200);
			$('#weixin').slideDown(200);
		},
		slidedown: function() {
			$('#person_user').slideDown(200);
			$('#weixin').slideUp(200);
		},
		//登陆
		login(ev) {
			ev.preventDefault()
			var that = this
			var data = { 'username': that.username, 'password': that.password, 'code': that.code };
			//验证输入手机号
			if(!this.username) {
				//					Toast('请输用户名');
				alert('1')
				return;
			}
			//				if(!(/^1[358]\d{9}$/.test(this.tel))) {
			//					Toast('手机号输入有误');
			//					return;
			//				}
			if(!this.password) {
				alert('1')

				//					Toast('请输入密码');
				return;
			}
			if(!this.code) {
				alert('1')

				//					Toast('请输入验证码');
				return;
			}

			$.ajax({
				url: 'urls/Login/login',
				dataType: 'json',
				type: 'post',
				data: {
					'username': this.username,
					'password': this.password,
					'code': this.code,
				},
				success: function(e) {

					if(e.status == 1) {
						that.$router.push('/main');

					} else {

					}
					return false;
				}
			})

		}
	},
	mounted: function() {
		this.getvercode()

	}

}



