
import XHeader from 'vux/src/components/x-header/index.vue'
;
import cal from '../cal.js'
export default {
	components: {
		XHeader

	},
	//						img_url: '../../static/flyda_img/mywork/3.png'
	data: function() {
		return {
			data: [],
			time: ''
		}

	},

	created: function() {

	},

	methods: {
		getddata: function() {
			var that = this
			$.ajax({
				url: 'urls/Mission/my_mission',
				dataType: 'json',
				type: 'post',
				data: {
					date: this.time
				},
				success: function(e) {

					if(e.status == 1) {

						that.data = e.data

					} else {

					}
					return false;
				}
			})

		},
		gettime: function() {
			var date = new Date()
			var month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : (date.getMonth() + 1)
			this.time = date.getFullYear() + '-' + month + '-' + date.getDate()
			alert(this.time)
		}

	},
	mounted() {
		this.gettime()
		$('.caldate').datepicker({})

		this.getddata()

	}
}



